/* 1. Theme default css */
@import url("https://fonts.googleapis.com/css2?family=Kiwi+Maru:wght@300;500&family=Train+One&display=swap");
body {
  font-family: "Kiwi Maru", serif;
  font-weight: normal;
  font-style: normal;
  color: #6e7797;
  font-size: 14px;
}

.img, img {
  max-width: 100%;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.fix {
  overflow: hidden;
}

a,
.button {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

a:focus,
.button:focus {
  text-decoration: none;
  outline: none;
}

a:focus,
a:hover {
  color: inherit;
  text-decoration: none;
}

a,
button {
  color: inherit;
  outline: medium none;
}

button:focus, input:focus, input:focus, textarea, textarea:focus {
  outline: 0;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

h1 {
  font-family: "Train One", cursive;
  font-weight: normal;
  color: #292f3b;
  margin-top: 0px;
  font-style: normal;
  font-weight: bold;
  text-transform: normal;
  margin-bottom: 0px;
  line-height: 1.5;
}

h2 {
  font-family: "Train One", cursive;
  color: #000000;
  margin-top: 0px;
  font-style: normal;
  text-transform: normal;
}

h3 {
  font-family: "Train One", cursive;
  font-weight: 500;
  color: #000000;
  margin-top: 0px;
  font-style: normal;
  font-weight: 400;
  text-transform: normal;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 65px;
  font-weight: 500;
}

h2 {
  font-size: 50px;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  h2 {
    font-size: 35px;
  }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
  h2 {
    font-size: 40px;
  }
}

h3 {
  font-size: 15px;
}

h4 {
  font-size: 22px;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  h4 {
    font-size: 16px;
  }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
  h4 {
    font-size: 15px;
  }
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none;
}

p {
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
  color: #6e7797;
  margin-bottom: 15px;
}

hr {
  border-bottom: 1px solid #eceff8;
  border-top: 0 none;
  margin: 30px 0;
  padding: 0;
}

label {
  color: #7e7e7e;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
}

*::-moz-selection {
  background: #333333;
  color: #ffffff;
  text-shadow: none;
}

::-moz-selection {
  background: #333333;
  color: #ffffff;
  text-shadow: none;
}

::selection {
  background: #333333;
  color: #ffffff;
  text-shadow: none;
}

